  },
 
 
  methods: {
    
    GetRangeDates(){
        var vm = this;
       // var getCurrentDate = moment(vm.monthStartDate,'YYYY/MM/DD');
        var getDateStartDate = moment(vm.monthStartDate).subtract(1,'months');
        var getMonthEndDate = moment(vm.monthEndDate)///.subtract(1,'months');
        var getFinalEndDate = moment(getMonthEndDate._d,'YYYY');
        vm.monthStartDate = parseInt(getDateStartDate.format('YYYY')) + '-' + parseInt(getDateStartDate.format('M')) + '-' + parseInt(getDateStartDate.format('D'));
        vm.monthEndDate = parseInt(getFinalEndDate.format('YYYY')) + '-' + parseInt(getFinalEndDate.format('M')) + '-' + parseInt(getFinalEndDate.format('D'));
        vm.monthRange = {
          start : new Date(vm.monthStartDate),
          end: new Date(vm.monthEndDate)
        }
    },  
