      var vm=this;
      vm.monthRange=[];
      vm.monthRange = {
          start : value.start,
          end: value.end
        }
        vm.monthStartDate = value.start
        vm.monthEndDate = value.end      
    },
    goToPreviousMonth() {
      var vm = this;
      if (vm.monthRange.start && vm.monthRange.end) {
        const newStartDate = new Date(vm.monthRange.start);
        const newEndDate = new Date(vm.monthRange.end);

        const diffTime = Math.abs(newEndDate - newStartDate);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 
        
        var AddOneMonthToStartDate = moment(newStartDate).subtract(diffDays+1,'days');
        var AddOneMonthToEndDate = moment(newStartDate).subtract(1,'days');

        var getFindStartDate = moment(AddOneMonthToStartDate._d,'YYYY');
        var getFinalEndDate = moment(AddOneMonthToEndDate._d,'YYYY');

        vm.monthStartDate = parseInt(getFindStartDate.format('YYYY')) + '-' + parseInt(getFindStartDate.format('M')) + '-' + parseInt(getFindStartDate.format('D'));
        vm.monthEndDate = parseInt(getFinalEndDate.format('YYYY')) + '-' + parseInt(getFinalEndDate.format('M')) + '-' + parseInt(getFinalEndDate.format('D'));
        vm.monthRange=[];
        vm.monthRange = {
          start : new Date(vm.monthStartDate),
          end: new Date(vm.monthEndDate)
        }
      }
    },
    goToNextMonth() {
      var vm = this;
      if (vm.monthRange.start && vm.monthRange.end) {

        const newStartDate = new Date(vm.monthRange.start);
        const newEndDate = new Date(vm.monthRange.end);
        const diffTime = Math.abs(newEndDate - newStartDate);
        const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24)); 
        //const dateDiff=newEndDate-newStartDate;
        
        var AddOneMonthToStartDate = moment(newEndDate).add(1,'days');
        var AddOneMonthToEndDate = moment(newEndDate).add(diffDays+1,'days');

        var getFindStartDate = moment(AddOneMonthToStartDate._d,'YYYY');
        var getFinalEndDate = moment(AddOneMonthToEndDate._d,'YYYY');

        vm.monthStartDate = parseInt(getFindStartDate.format('YYYY')) + ',' + parseInt(getFindStartDate.format('M')) + ',' + parseInt(getFindStartDate.format('D'));
        vm.monthEndDate = parseInt(getFinalEndDate.format('YYYY')) + ',' + parseInt(getFinalEndDate.format('M')) + ',' + parseInt(getFinalEndDate.format('D'));
        vm.monthRange=[];
        vm.monthRange = {
          start : new Date(vm.monthStartDate),
          end: new Date(vm.monthEndDate)
        }
      }
    },
    ConvertDateFormat(date) {   
      var newDate = new Date(date);
      var mnth = ("0" + (newDate.getMonth() + 1)).slice(-2);
      var day = ("0" + newDate.getDate()).slice(-2);
      var datavalue = [newDate.getFullYear(), mnth, day].join("-");
      var date= datavalue + " 23:59:59";
      return new Date(date);
    },
    getTabIcon(name) {
      switch (name) {
        case "UserTrackingReport":
          return "fa fa-calendar";
        case "ExceededTimeReport":
          return "fa fa-clock-o";
        case "LateEmployees":
          return "fa fa-user-circle-o";
        case "UnproductiveAppsReport":
          return "fa fa-info-circle";
        case "IdleTimeReport":
          return "fa fa-clock-o";
        case "AppwebsiteusageReport":
          return "fa fa-desktop";
        case "CategoryReport":
          return "fa fa-file-text";
        default:
          return "";
      }
    },
    activateTab(index) {
      this.activeKey = index;
      //this.GetRangeDates();
    },
    // async onMonthSelected(date) {
    //   debugger
    //   var vm = this;
