    //   ).getDate();
    //   const thirtyDaysLater = new Date(vm.monthStartDate);
    //   thirtyDaysLater.setDate(vm.monthStartDate.getDate() + currentMonthDays);
    //   const diffTime = Math.abs(vm.monthEndDate - vm.monthStartDate);
    //   const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
    //   var dateBeforeThirtyDays = new Date();
    //   dateBeforeThirtyDays.setDate(
    //     dateBeforeThirtyDays.getDate() - currentMonthDays
    //   );
    //   vm.updateMonthRange = true;
    //   // if (diffDays < 27) {
    //   //   vm.updateMonthRange = true;
    //   //   vm.ShowAlert(
    //   //     "Please select atleast 30 days.",
    //   //     "warning",
    //   //     false,
    //   //     vm.$t("Alert")
    //   //   );
    //   //   vm.monthEndDate = new Date();
    //   //   vm.monthStartDate = dateBeforeThirtyDays;
    //   // } else if (vm.monthEndDate > thirtyDaysLater) {
    //   //   vm.updateMonthRange = true;
    //   //   vm.ShowAlert(
    //   //     "You can not select more than 30 days.",
    //   //     "warning",
    //   //     false,
    //   //     vm.$t("Alert")
    //   //   );
    //   //   vm.monthEndDate = new Date();
    //   //   vm.monthStartDate = dateBeforeThirtyDays;
    //   // }
    // },
    formatDuration(durationInHours) {
      const hours = Math.floor(durationInHours);
      const minutes = Math.round((durationInHours - hours) * 60);
      return `${hours}h ${minutes}m`;
    },
    getTimeRanges(interval) {
      const ranges = [];
      const date = new Date();
      const format = {
        hour: "numeric",
        minute: "numeric",
      };
      for (let minutes = 0; minutes < 24 * 60; minutes = minutes + interval) {
        date.setHours(0);
        date.setMinutes(minutes);
        ranges.push(date.toLocaleTimeString("en", format));
      }
      return ranges;
    },
