<template>
  <div>
    <dynamic-form lang="en"  :buttons="buttons"  :schema="FormSchema" v-on:OnSubmit="onSubmit">
      <template v-slot:tgslot-IsShiftView="{ data }">
          <div class="mt-2 labelalign col-control-right ">
            <label
       v-bind:class="{ 'switch': true, 'switch enabled': (data.fieldInfo.value == true) }">
       <input type="checkbox" v-model="data.fieldInfo.value"
           v-bind:id="'isTrueCheck'"
           class="form-check-input rdoticketfor custom-control-input dynamic">
       <span class="slider round">
           <span class="slider-yes">Shift View</span>
           <span class="slider-no">Day View</span>
              </span>
          </label>
        </div>
        </template>
    </dynamic-form>
    <div class="row placeholder">
      <div class="col-md-12 p-0">
          <ReportViewer  v-if="parameter.length>0" :parameter="parameter" :reportPath="reportPath"></ReportViewer>
      </div>
  </div> 
  </div>
</template>

<script> 
import DataService from '../../services/DataService';
import ReportViewer from '../TimerReports/Reportviewer.vue';
export default {
  components: {
      ReportViewer
  },
  props:{
  fromDate: {
    type:Object
  },
    ToDate: {
      type:Object
  },
},
  data() {
    return {
      reportPath: null,
          parameter: [],
      FormSchema: [
        {
          layoutType: "four",
          Data: [
           
                    {
                          astype: "MultiSelectField",
                          label: this.$t("Appweb"),
                          name: "Appweb",
                          value: "",
                          mode: "tag",
                          placeholder: this.$t("SelectApplicationWebsite"),
                          config: {
                              options: [],

                          },
                         
                      },
                      {
                          astype: "MultiSelectField",
                          label: this.$t("Department"),
                          name: "Department",
                          value: "",
                          mode: "tag",
                          placeholder: this.$t("SelectDepartment"),
                          config: {
                              options: [],

                          },
                         
                      },
                      {
                          astype: "MultiSelectField",
                          label: 'Users',
                          name: "User",
                          value: "",
                          mode: "tag",
                          placeholder: this.$t("SelectUsers"),
                          config: {
                              options: [],

                          },
                         
                      },
                      {
                            astype: "SlotField",
                             label: this.$t('ShiftDayView'),
                            name: "IsShiftView",
                            value: "",
                            placeholder: "",
                            
                        },
            
          ],
        },
      ],
      buttons: [
        {
          type: "submit",
          class: "btn btn-success",
          text: "<i class='fa fa-search pr-2'></i>Search",
        },

        {
          type: "button",
          class: "btn btn-danger",
          text: "<i class='fa fa-close pr-2'></i>Clear all",
          onClick: this.ClearAll
        },
      ],
     
    };
    
  },
  created(){
     this.BindListing();
  },
  methods: {
    
    BindListing: async function () {
    
    var vm = this;


    var url=`pageSize=1000&sortBy=ApplicationName&sortExp=ASC`
      if (vm.FormSchema[0].Data[0].label == 'Application/Website') {
        await DataService.GetScreenShotListing(url).then(response => {
          var jsondata = response.data
          if (jsondata != null) {
            jsondata.forEach(function (item, index) {
              (vm.FormSchema)[0].Data[0].config.options.push({
                name: item.application_display_name,
                value: item.ApplicationId
              });
            });
          }
        });
      }
    if(vm.FormSchema[0].Data[1].label == 'Department')
    {
      var params = `id=&modulename=Timer&type=DEPARTMENT&search=&refId=`;  
    await DataService.GetDDLData(params).then (response=> {
      
      var jsondata = response.data.DATA
      //var jsondata = Object.entries(json);
      if (jsondata != null) {
        jsondata.forEach(function (item, index) {
          (vm.FormSchema)[0].Data[1].config.options.push({
            name: item.name,
            value: item.value
          })
        })
      }
    });
    }
    if(this.FormSchema[0].Data[2].label == 'Users'){
      var params = `id=&modulename=Timer&type=USERS&search=&refId=`;  
    await DataService.GetDDLData(params).then (response=> {
      
      var jsondata = response.data.DATA
      //var jsondata = Object.entries(json);
      if (jsondata != null) {
        jsondata.forEach(function (item, index) {
          (vm.FormSchema)[0].Data[2].config.options.push({
            name: item.name,
            value: item.value
          })
        })
      }
    });
    }
   
    },
    async onSubmit(e) {
          var vm = this;
          vm.reportPath = '';
          vm.parameter = [];
          let myObj = {
              reportName: "TimerAppWebsiteUsageReport",
              userIds: e.User.length > 0 ? e.User.map(item => item.value).join(","): null,
              DeparmentIds:e.Department.length > 0? e.Department.map(item => item.value).join(","): null,
              ApplicationIds:e.Appweb.length > 0? e.Appweb.map(item => item.value).join(","): null,
              dateFrom:vm.$options.filters.formatDateYear(vm.fromDate),
              dateTo:vm.$options.filters.formatDateYear(vm.ToDate),
              IsShiftView:e.IsShiftView==false ? 'NO' : 'YES'
          }
          await DataService.ReportMain(myObj).then(function (response) {
              console.log(response);
              vm.reportPath = response.data.reportpath;
              vm.parameter = response.data.parameters;
          })
      },
      ClearAll: function () {
          var vm = this;
          (vm.FormSchema)[0].Data[0].value = '';
          (vm.FormSchema)[0].Data[1].value = '';
          (vm.FormSchema)[0].Data[2].value = '';
          vm.parameter = [];
      },
  }
  
}
</script>